.itemCont {
  display: grid;
  color: white;
  border-bottom: solid 0.5rem white;
}
.itemCont video {
  width: 80%;
}
.itemCont {
  background-color: rgb(60, 60, 60);
}
.siteName {
  height: 12vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  margin-top: 3rem;
}
video::-webkit-media-controls {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  margin-top: 10px;
}
.videoWrapper img {
  width: 3rem;
}
.videoWrapper {
  position: relative;
  margin: 0 auto;
  width: 80%;
}
.videoPlayer {
  display: block;
  margin: 0 auto;
  border-radius: 0.5rem;
  border: 0.2rem transparent solid;
  background: linear-gradient(
    58deg,
    rgb(72, 73, 74) 12%,
    rgb(157, 161, 156) 84%
  );
}
.playButton {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;
  cursor: pointer;
}
.container,
.features,
.tools {
  padding: 0.5rem;
}
.tools p,
.features p {
  text-align: left;
}
.features ul {
  list-style-type: circle;
}
.features li {
  text-align: left;
}
.tools ul {
  display: grid;
  list-style-type: circle;
  grid-template-columns: repeat(2, 1fr);
}

.tools li {
  text-align: left;
}
.bottomCont {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  padding: 0.5rem;
  text-align: center;
}
.idPassword {
  font-size: 0.5rem;
}
.github {
  width: 2rem;
}
.www {
  width: 1.8rem;
}
.demo a {
  color: white;
  margin: 0;
  font-size: 0.8rem;
  text-decoration: none;
}
.demo:visited {
  color: white;
}

.desktopPics {
  width: 90%;
  display: grid;
  grid-template-columns: 1fr;
  grid-row: auto;
  gap: 0.5rem;
  margin: 0 auto;
  padding: 2rem 0;
}
.desktopHeader,
.desktopCategoryHead {
  font-size: 0.8rem;
  margin-bottom: 0.5rem;
}
.desktopPics img {
  width: 100%;
}
@media only screen and (max-width: 940px) and (orientation: landscape) {
  .itemCont {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
  .videoWrapper {
    width: 50%;
    grid-column: 2/3;
    grid-row: 1/2;
    padding: 0.5rem;
    margin-top: 1rem;
  }

  .siteName {
    grid-column: 1/2;
    grid-row: 1/2;
    font-size: 1.5rem;
    align-self: center;
    margin-top: 0;
  }
  .container {
    padding: 1rem;
  }
  .desc {
    grid-column: 1/2;
    grid-row: 2/3;
  }
  .features {
    grid-column: 1/2;
    grid-row: 3/4;
    /* border-right: 0.1rem solid white;
    border-top: 0.1rem solid white; */
    padding: 1rem;
  }
  .tools {
    grid-column: 2/3;
    grid-row: 3/4;
    /* border-top: 0.1rem solid white; */
    padding: 1rem;
  }
  .desc,
  .features,
  .tools {
    font-size: 0.8rem;
  }

  .bottomCont {
    width: 80%;
    grid-column: 2/3;
    grid-row: 2/3;
    align-self: center;
    align-items: center;
    padding: 1rem;
  }
  .github {
    width: 1.8rem;
  }
  .www {
    width: 1.6rem;
  }
  .desktopPics {
    grid-column: 1/-1;
    grid-row: 4/5;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-row: auto;
  }
  .desktopHeader,
  .desktopCategoryHead {
    font-size: 0.7rem;
  }
}
@media only screen and (min-width: 941px) {
  .itemCont {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    /* grid-template-rows: 6rem 0.7fr auto; */
    grid-template-rows: auto;
    color: white;
    border-bottom: none;
    /* padding: 2rem; */
    margin-bottom: 6rem;
    margin-top: 6rem;
    background: rgb(249, 196, 62);
    background: linear-gradient(
      110deg,
      rgba(249, 196, 62, 0.7) 50%,
      rgba(250, 154, 57, 0.7) 50%,
      rgba(249, 196, 62, 0.7) 75%
    );
    /* height: 60rem; */
  }
  .desktopPics {
    grid-column: 1/-1;
    grid-row: 4/5;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-row: auto;
    margin: 3rem auto;
    width: 95%;
    padding: 0;
  }
  .desktopPics img:hover {
    transition: transform 0.5s ease-in-out;
  }
  .desktopPics img:hover {
    transform: scale(1.8);
  }
  .desktopHeader,
  .desktopCategoryHead {
    font-size: 0.7rem;
  }
  .siteName {
    width: 100%;
    font-size: 3rem;
    color: rgb(255, 230, 184);
    display: block;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    margin: 1rem 0 0 2rem;
    grid-column: 2/-1;
    grid-row: 1/2;
  }
  .desc {
    color: white;
    margin: 0;
    width: 70%;
    font-size: 1rem;
  }
  .container {
    grid-column: 2/3;
    grid-row: 2/3;
    padding: 0;
    padding-left: 2rem;
  }
  .features {
    grid-column: 3/-1;
    grid-row: 2/3;
    font-size: 0.8rem;
    padding: 0;
    width: 90%;
  }
  .features p,
  .tools p {
    margin: 0;
  }
  .tools {
    grid-column: 3/-1;
    grid-row: 3/4;
    font-size: 0.8rem;
    width: 90%;
    padding: 0;
  }
  .samplePicCont {
    grid-column: 1/2;
    grid-row: 1/4;
    align-self: center;
    padding-top: 2rem;
  }

  .bottomCont {
    grid-column: 2/3;
    grid-row: 3/4;
    align-content: end;
    padding: 0;
    padding-bottom: 1rem;
  }
  .bottomCont p {
    margin: 0;
    font-size: 0.8rem;
  }
  .bottomCont a:hover {
    transform: scale(1.3);
  }
  .itemCont video {
    width: 100%;
    box-shadow: 34px 34px 60px -20px rgba(0, 0, 0, 0.45),
      25px 0px 20px -20px rgba(0, 0, 0, 0.45);

    border-radius: 0.5rem;
  }
  /* .videoWrapper img {
    width: 3rem;
  }
  .videoWrapper {
    position: relative;
    width: fit-content;
    padding-left: 1rem;
  }
  .videoPlayer {
    border-radius: 0;
    border: none;
    background: transparent;
  } */
}
