.connectIcons {
  position: fixed;
  z-index: 1;
  top: 45%;
  right: 0;
  /* -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%); */
  border-radius: 0.3rem 0 0 0.3rem;
  /* background-color: rgb(232, 117, 1); */
  background-color: white;
  box-shadow: 0rem 0.25rem 0.5rem 0 rgb(115, 113, 113);
  align-content: center;
}
.bigcont {
  align-self: center;
}
.iconCont {
  width: 2rem;
  text-align: center;
  padding-left: 0;
}
.listitem {
  list-style: none;
  text-align: center;
}
.github {
  width: 2.1rem;
  padding-bottom: 0.5rem;
}
.linkedin {
  width: 1.7rem;
}
.mail {
  width: 1.8rem;
  padding-bottom: 0.5rem;
}

@media only screen and (max-width: 940px) and (orientation: landscape) {
  .connectIcons {
    top: 30%;
  }
  .iconCont {
    margin: 0;
  }
}

@media only screen and (min-width: 941px) {
  .connectIcons {
    display: none;
  }
}
