.container {
  width: 100%;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: max-content max-content;

  position: fixed;
  top: 0;
  z-index: 2;
}
.logoCont {
  grid-column: 1/-1;
  grid-row: 1/2;
  display: grid;
  grid-template-columns: 2fr 1fr 2fr;
  padding-top: 0.5rem;
  font-size: 0.8rem;
  background-color: white;
  width: 100%;
}
.logopic {
  width: 90%;
  grid-column: 1/2;
  /* padding: 0.5rem; */
}
.fernCont {
  width: 100%;
  grid-column: 2/3;
  display: grid;
}
.silverfern {
  width: 50%;
  opacity: 0.7;
  align-self: center;
}
.job {
  padding-left: 0.1rem;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  font-size: 0.8rem;

  align-self: center;
  text-align: right;
  padding-right: 0.5rem;
  color: rgb(60, 60, 60);
}

.rightCont {
  grid-column: 1/-1;
  grid-row: 2/3;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: max-content;
  gap: 0.2rem;
  justify-self: right;
  font-size: 0.9rem;
  width: 100%;
}
.cv,
.contact {
  background-color: rgb(49, 150, 157);
  padding: 0.2rem;
  align-self: center;
  box-shadow: 0px 14px 109px 0px rgba(0, 0, 0, 0.25),
    0px 10px 10px 0px rgba(0, 0, 0, 0.22);
  color: white;
  border-radius: 0 0 0.2rem 0.2rem;
}

.aboutMe,
.tech {
  background-color: rgb(49, 150, 157);
  padding: 0.2rem;
  align-self: center;
  box-shadow: 0px 14px 109px 0px rgba(0, 0, 0, 0.25),
    0px 10px 10px 0px rgba(0, 0, 0, 0.22);
  color: white;
  border-radius: 0 0 0.2rem 0.2rem;
}
.contact {
  grid-column: 4/5;
}
.contact1 {
  display: none;
}

.cv a,
.contact a {
  align-self: flex-start;
  text-decoration: none;
  color: white;
}
.cv a:visited,
.contact a:visited {
  color: white;
  text-decoration: none;
}

.github {
  display: none;
}
.linkedin {
  display: none;
}

