/*Desktop*/
@media only screen and (min-width: 941px) {
  .container {
    width: 100%;
    /* height: 100%; */
    position: relative;
    background-color: white;
    display: grid;
    grid-template-columns: 1fr 2fr 1fr;
    grid-template-rows: 1fr;
    padding: 0;
    margin: 0 auto;
    /* padding-top: 0.3rem; */

    /* border-radius: 1rem 1rem 0 0; */
    /* border-radius: 0.5rem; */
  }
  .logoCont {
    display: grid;
    grid-template-columns: 2fr 0.5fr;
    margin-top: 0;
    padding: 0;
    background-color: white;
    grid-column: 1/2;
    box-shadow: none;
    width: 80%;
    padding-left: 1.5rem;
    border-radius: 1rem 0 0 0;
  }
  .logoCont a {
    align-self: end;
    grid-column: 1/2;
    grid-row: 1/2;
  }
  .logopic {
    width: 100%;
  }
  .silverfern {
    grid-column: 2/3;
    grid-row: 1/2;
    align-self: center;
    width: 100%;
  }
  .job {
    margin-bottom: 0;
    align-self: center;
    text-align: left;
    grid-column: 1/2;
    grid-row: 2/3;
    text-align: center;
    font-size: 0.6rem;
  }
  .rightCont {
    grid-column: 2/3;
    grid-row: 1/2;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    margin: 0 auto;
    width: 60%;
    gap: 0;
    height: 90%;
    font-size: 0.8rem;
    align-self: center;
    align-content: center;
    justify-self: right;
    /* padding-right: 0.5rem; */
  }
  .cv,
  .aboutMe,
  .tech {
    background-color: transparent;
    padding: 0.2rem;
    align-self: center;
    box-shadow: none;
    color: rgb(60, 60, 60);
  }
  .contact {
    display: none;
  }

  .contact1 {
    display: block;
    background-color: rgb(60, 60, 60);
    box-shadow: none;
    border-radius: 0.2rem;
    width: 5rem;
    align-self: center;
    justify-self: end;
    grid-column: 3/4;
    margin-right: 1.5rem;
    font-size: 0.8rem;
    padding: 0.1rem;
  }
  .contact1 a {
    color: white;
    text-decoration: none;
  }

  .contact1 a:visited {
    color: white;
    text-decoration: none;
  }
  .contact1 a:hover,
  .contact1:hover {
    color: white;
    background-color: orange;
  }

  .cv a {
    align-self: flex-start;
    text-decoration: none;
    color: rgb(60, 60, 60);
  }
  .cv a:visited {
    color: rgb(60, 60, 60);
    text-decoration: none;
  }

  .cv a:hover,
  .github a:hover,
  .linkedin a:hover,
  .aboutMe:hover,
  .tech:hover {
    color: white;
    background-color: rgb(65, 166, 197);
    /* border: 0.1rem solid orange; */
    border-radius: 0.2rem;
    padding: 0.2rem;
  }
  .github,
  .linkedin {
    /* background-color: rgb(49, 150, 157); */
    border-radius: 0;
    padding: 0.2rem;
    align-self: flex-start;
    display: inline;
  }
  .github a,
  .linkedin a {
    /* background-color: rgb(49, 150, 157); */
    border-radius: 0;
    padding: 0.2rem;
    align-self: flex-start;
    display: inline;
    text-decoration: none;
    color: rgb(60, 60, 60);
  }
}
