.itemCont {
  background-color: white;
  padding: 1rem;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 0.5fr 2fr 1fr 1.5fr 0.5fr;
}
.siteName {
  font-size: 1.2rem;
  text-align: left;
  padding: 0.5rem;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  grid-column: 1/-1;
  grid-row: 1/2;
}
.samplePicCont {
  display: grid;
  grid-template-rows: max-content 1fr;
}
.seeMore {
  margin: 0;
  background: rgb(217, 139, 18);
  background: linear-gradient(
    180deg,
    rgba(217, 139, 18, 1) 0%,
    rgba(235, 177, 55, 1) 50%,
    rgba(217, 139, 18, 1) 100%
  );
  color: white;
  padding: 0.1rem;
  margin: 0;
  text-align: center;
  width: 35%;
  font-size: 0.8rem;
  margin-bottom: -4rem;
  align-self: center;
}
.samplePic {
  width: 100%;
  grid-column: 1/-1;
  grid-row: 2/3;
}
.pic {
  width: 80%;
  padding-top: 0.5rem;
  grid-column: 1/-1;
  grid-row: 3/4;
}
.pCont {
  padding: 0 1rem;
  font-size: 0.8rem;
  grid-column: 1/-1;
  grid-row: 4/5;
  margin-top: 1rem;
}
.desc {
  margin: 0 auto;
}
.bottomCont {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  padding-top: 0.5rem;
  padding-bottom: 1rem;
  grid-column: 1/-1;
  grid-row: 5/6;
}
.github {
  width: 2rem;
}
.www {
  width: 1.8rem;
}
.demo a {
  color: rgb(60, 60, 60);
  margin: 0;
  font-size: 0.8rem;
  text-decoration: none;
}
.demo:visited {
  color: rgb(60, 60, 60);
}
@media only screen and (max-width: 940px) and (orientation: landscape) {
  .itemCont {
    grid-template-columns: 2fr 1fr;
    grid-template-rows: max-content 1fr max-content;
    width: 80%;
    margin: 0 auto;
  }
  .samplePicCont {
    grid-column: 1/2;
    grid-row: 2/-1;
  }
  .samplePic {
    width: 80%;
    grid-column: 1/-1;
    grid-row: 2/3;
  }
  .pic {
    display: none;
  }
  .pCont {
    grid-column: 2/-1;
    grid-row: 2/3;
    text-align: left;
  }
  .bottomCont {
    grid-column: 2/-1;
    grid-row: 3/4;
    align-items: center;
  }
  .bottomCont p {
    margin: 0;
    font-size: 0.7rem;
  }
  .github {
    width: 1.5rem;
  }
  .www {
    width: 1.4rem;
  }
}
@media only screen and (min-width: 941px) {
  .itemCont {
    margin: 0;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
  }
  .container {
    display: none;
    /* max-height: 8rem;
    display: grid;
    margin: 0 auto;
    text-align: center; */
  }
  .siteName {
    display: none;
  }
  .samplePicCont {
    width: 100%;
  }
  .seeMore {
    display: none;
  }
  .samplePic {
    grid-row: 1/2;
  }

  .pic {
