.bigCont {
  text-align: center;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto;
  color: #2b2c34;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  /* background-image: url("../images/pexels-vlado-paunovic-4372705.jpg"); */
  background-image: url("../images/Cartoon4 3.webp");
  width: 100vw;
  background-attachment: fixed;
  /* background-position: center; */
  background-size: 100vw;
  background-repeat: no-repeat;
}
.intro {
  height: 100vh;
}
.about {
  padding-top: 1rem;
  background-color: rgb(60, 60, 60);
  padding-bottom: 2rem;
  margin-top: -0.1rem;
}
.buttonToTop {
  margin: 0 auto;
  width: 100%;
  text-align: right;
  margin-top: 2rem;
  position: fixed;
  z-index: 1;
  bottom: 0;
  right: 0;
  padding-bottom: 0.5rem;
}
.backToTopButton {
  background-color: orange;
  border: none;
  border-radius: 1rem;
  text-align: right;
  margin: 0 auto;
}
.backToTopButton img {
  width: 1.5rem;
  padding: 0.08rem;
}
.carousel {
  display: none;
}
.techCarousel {
  display: none;
}

@media only screen and (max-width: 940px) and (orientation: landscape) {
  .intro {
    height: 50%;
  }
  .bigCont {
    background-image: none;
  }
}
