.carouselCont {
  width: 90%;
  margin: 0 auto;
  background-color: rgb(94, 92, 92, 0.3);
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(8, 1fr);
  padding-top: 1rem;
}

.iconTitle {
  font-size: 0.5rem;
  margin: 0 auto;
  align-content: center;
}
.iconTitle img {
  width: 2rem;
  padding: 0.3rem;
  opacity: 0.4;
  text-align: center;
  rotate: 45deg;
}
.iconTitle p {
  display: none;
}
