.carouselCont {
  width: 100%;
  /* height: auto; */
  margin-bottom: 2rem;
  /* margin-top: 3rem; */
  margin: 0 auto;
  /* padding-top: 1rem;
  padding-bottom: 1rem; */
  background: rgb(152, 152, 152);
  background: linear-gradient(
    0deg,
    rgba(152, 152, 152, 1) 0%,
    rgba(230, 228, 228, 1) 51%,
    rgba(255, 255, 255, 1) 51%
  );
  position: relative;
}

.carouselCont img {
  width: 90%;
  margin: 3rem;
  border-radius: 0.5rem;
  /* rotate: -10deg; */
  /* padding: 4rem; */
  /* margin: 0.5rem; */
  box-shadow: 0.25rem 0rem 0.4rem 0 rgb(93, 91, 91);
  border: 0.2rem transparent solid;
  background: linear-gradient(
    58deg,
    rgb(72, 73, 74) 12%,
    rgb(157, 161, 156) 84%
  );
}
.carouselCont img:hover {
  rotate: 0deg;
  transition: transform 0.5s ease-in-out;
}
.carouselCont img:hover {
  transform: scale(1.2);
}
