.mainCont {
  text-align: left;
  margin: 1rem;
  padding: 1.5rem;
  color: rgb(60, 60, 60);
  background-color: white;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
.aboutHead {
  color: rgb(60, 60, 60);
  text-align: left;
  font-size: 1.2rem;
  background-color: white;
}

.myPic {
  grid-row: 1/2;
  grid-column: 2/3;
  text-align: right;
}
.myPic img {
  width: 4rem;
  border-radius: 30% 70% 56% 44% / 30% 22% 78% 70%;
  rotate: 12deg;
  box-shadow: 0.25rem 0rem 0.4rem 0 rgb(93, 91, 91);
}
.mainText {
  grid-column: 1/-1;
  font-size: 0.8rem;
}
@media only screen and (min-width: 941px) {
  .mainCont {
    padding: 2rem;
    background-color: white;
    grid-template-columns: repeat(4, 1fr);
    margin: 0;
  }
  .aboutHead {
    color: rgb(60, 60, 60);
    padding-left: 0;
    font-size: 3.5rem;
    background-color: white;
    margin: 0;
  }

  .myPic {
    grid-row: 1/-1;
    grid-column: 4/5;
    text-align: center;
  }
  .myPic img {
    width: 50%;
    rotate: 10deg;
  }
  .mainText {
    grid-column: 2/4;
    width: 100%;
    padding: 1rem 1rem 2rem 2rem;
  }
}
