.head {
  margin: 0 auto;
  /* background-image: url("../images/pexels-laura-tancredi-7078717.jpg"); */
  background-image: url("../images/pexels-karolina-grabowska-5202288.webp");
  background-color: gray;
  min-height: 35vw;
  background-attachment: fixed;
  background-position: top;
  background-size: cover;
}
.contactCont {
  width: 90%;
  margin: 0 auto;
  margin-bottom: 4rem;
  padding-top: 3rem;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  padding-bottom: 1rem;
}
.container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  width: 100%;
}
/* a {
  text-decoration: none;
  color: black;
}
a:visited {
  color: black;
} */

.getintouch {
  padding-top: 1.5rem;
  color: white;
  font-weight: 200;
  font-size: 1.5rem;
  text-align: center;
}
.contactForm {
  width: 90%;
  margin: 0 auto;
}
.name,
.email,
.textarea {
  border-style: solid;
  border-width: 0.08rem;
  border-color: rgb(60, 60, 60);
  /* border-radius: 0.2rem; */
  height: 2rem;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.8);
}
.email {
  margin-top: 0.5rem;
}
.textarea {
  padding-top: 0.5rem;
  margin-top: 0.5rem;
  height: 30vh;
}
.button {
  background-color: rgb(241, 119, 27);
  color: white;
  font-size: 0.8rem;
  border: none;
  padding: 0.3rem;
  border-radius: 0.3rem;
  margin-top: 0.5rem;
}
.button:hover {
  background-color: rgb(218, 103, 21);
}
@media (orientation: landscape) {
  .contactCont {
    padding-top: 3rem;
  }
  .getintouch {
    padding-top: 0;
    font-size: 1rem;
  }

  .input {
    display: grid;
    width: 100%;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: auto;
    gap: 0.9rem;
    padding-top: 0;
    margin-right: 0;
  }
  .name {
    width: 100%;
    grid-column: 1/2;
    margin-bottom: 0.2rem;
  }
  .email {
    width: 100%;
    grid-column: 2/3;
    margin-bottom: 0.2rem;
    margin-top: 0;
    justify-self: end;
  }
  .textarea {
    grid-column: 1/3;
    width: 99%;
    /* height: 7rem; */
  }
}

@media only screen and (min-width: 941px) {
  .contactCont {
    width: 50%;
    height: 100vh;
    margin-top: 5rem;
  }
  .rightCont {
    align-items: right;
    margin-top: 1rem;
    width: 50%;
  }
  .logopic {
    width: 10rem;
    padding: 0.5rem;
  }
  .silverfern {
    width: 2.2rem;
  }
  .rightCont {
    padding-top: 0.5rem;
    font-size: 1rem;
  }
  .contactForm {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: auto;
    margin: 0 auto;
  }
  .input {
    display: block;
    padding-top: 0;
    margin-right: 0;
  }
  .textarea,
  .name,
  .email {
    width: 100%;
  }
  .button {
    width: 5rem;
  }
}
