.cont {
  height: 100vh;
  display: grid;
  justify-content: left;
  align-items: center;
  position: relative;
  color: rgb(60, 60, 60);
}
.introCont {
  width: 70vw;
  color: rgba(255, 255, 255, 0.9);
  margin-left: 1rem;
  align-self: center;
  padding-top: 1.8rem;
}
.introCont1 {
  display: none;
}
.rightIntroCont {
  display: none;
}
.fadeContainer {
  opacity: 0;
  transition: opacity 0.5s ease-in-out;
  /* font-size: 1.3rem; */
  text-align: left;
  text-shadow: 0.2rem 0.2rem 0.2rem rgb(28, 26, 26);
}
.introCont strong {
  font-size: 1.7rem;
}

.fadeIn {
  opacity: 1;
}
.goToProjects {
  display: none;
}
/* .banner {
  width: 15rem;
  padding-left: 0.5rem;
} */
@media (max-width: 940px) and (orientation: landscape) {
  .cont {
    /* display: grid;
    justify-content: left; */
    align-items: normal;
    /* position: relative;
    color: rgb(60, 60, 60); */
    height: 50vh;
  }
  .introCont {
    display: none;
  }
  .introCont1 {
    display: inline;
    width: 50%;
    /* color: rgb(60, 60, 60);
    margin-left: 0;
    padding: 0; */
    margin-top: 1rem;
    /* position: relative; */
    justify-self: center;
    /* padding-right: 2rem; */
  }
  .fadeContainer1 {
    font-size: 1rem;
    text-shadow: 0;
    width: 90%;
    text-align: right;
  }
  .introCont1 strong {
    font-size: 1.5rem;
    font-weight: bold;
  }
}
/*  Desktop */
@media only screen and (min-width: 941px) {
  .banner {
    /* width: 15rem; */
    padding-left: 2.5rem;
    padding-right: 1rem;
    margin-top: 1rem;
    align-self: center;
  }
  .cont {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 2fr 1fr;
    height: 100%;
  }
  .introCont {
    display: none;
  }
  .rightIntroCont {
    display: inline;
    width: 50%;
    color: rgb(60, 60, 60);
    margin-left: 0;
    padding: 0;
    margin-top: 0;
    position: relative;
    justify-self: left;
    padding-left: 6rem;
    font-size: 0.8rem;
  }
  .rightIntroCont p {
    text-align: left;
  }
  .contact {
    /* background-color: aqua; */
    width: 5rem;
    padding: 0.1rem;
    border-radius: 0.2rem;
    font-size: 0.8rem;
    border: solid 0.1rem rgb(60, 60, 60);
  }
  .contact a {
    color: rgb(60, 60, 60);
    text-decoration: none;
  }
  .contact a:visited {
    color: rgb(60, 60, 60);
  }

  .introCont1 {
    display: inline;
    width: 70%;
    color: rgb(60, 60, 60);
    margin-left: 0;
    padding: 0;
    margin-top: 0;
    position: relative;
    justify-self: right;
    padding-right: 2rem;
  }

  .fadeContainer1 {
    /* opacity: 0;
    transition: opacity 0.5s ease-in-out; */
    font-size: 1.5rem;
    text-shadow: 0;
    width: 100%;
    text-align: right;
  }
  .introCont1 strong {
    font-size: 2rem;
    font-weight: bold;
  }
  .goToProjects {
    display: inline;
    grid-column: 1/-1;
    grid-row: 2/3;
    margin-top: 0;
    align-self: start;
  }
}
