.bigCont {
  background-color: white;
}
.projectCont {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto;
}
.titleCont {
  color: rgb(60, 60, 60);
  margin-bottom: -0.1rem;
  padding-top: 1rem;
}

.title {
  font-size: 1.2rem;
  text-align: center;
  width: 90%;
  margin: 0 auto;
  margin-top: 4rem;
}
.subHeading {
  width: 90%;
  font-size: 0.8rem;
  font-weight: 400;
  margin: 0 auto;
  margin-top: 1rem;
  margin-bottom: 3rem;
}
.itemCont {
  background-color: rgb(125, 168, 110);
  margin: 1rem;
  border-radius: 0.5rem;
  align-items: start;
}
.card {
  border-bottom: 1rem rgb(72, 71, 71) solid;
}
.cardName {
  display: none;
}
.pic {
  width: 100%;
  margin-top: -2rem;
  align-items: start;
}
.pCont {
  padding: 0 1rem;
}
.desc {
  margin: 0;
}
.bottomCont {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  padding-top: 0.5rem;
}
.github {
  width: 2rem;
}
.www {
  width: 1.8rem;
}
.ayPic {
  width: 50%;
}
@media (max-width: 940px) and (orientation: landscape) {
  .subHeading {
    width: 50%;
  }
}
@media only screen and (min-width: 941px) {
  .bigCont {
    background-color: white;
    box-shadow: 0.1rem 0.1rem 0.3rem 0 rgb(122, 121, 121);
  }
  .titleCont {
    display: none;
  }
  .projectCont {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: auto;
    gap: 0.5rem;
    width: 100%;
    background-color: none;
  }
  .titleCont {
    color: white;
    margin-bottom: 0;
  }
  .projectCont {
    width: 100%;
  }
  .title {
    font-size: 1.2rem;
    text-align: center;
    width: 50%;
    margin: 0 auto;
    margin-top: 2rem;
    border: 0.1rem solid white;
  }
  .subHeading {
    width: 50%;
    font-size: 0.8rem;
    font-weight: 400;
    margin: 0 auto;
    margin-top: 1rem;
    margin-bottom: 3rem;
  }
  .itemCont {
    background-color: rgb(125, 168, 110);
    margin: 0;
    border-radius: 0.5rem;
    align-items: start;
  }

  .card {
    position: relative;
  }

  .cardName {
    display: block;
    padding: 2rem;
    height: 2rem;
    color: rgb(182, 12, 12);
    font-weight: bold;
  }

  .pic {
    width: 100%;
    margin-top: -2rem;
    align-items: start;
  }
  .pCont {
    display: none;
  }
  .desc {
    display: none;
  }
  .bottomCont {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    padding-top: 0.5rem;
  }
  .github {
    width: 2rem;
  }
  .www {
    width: 1.8rem;
  }
  .ayPic {
    width: 50%;
  }
  .about {
    padding-top: 1rem;
    background-color: rgb(60, 60, 60);
    padding-bottom: 2rem;
    margin-top: -0.1rem;
  }
}
