.mainCont {
  background-color: none;
  width: 100%;
  text-align: center;
  margin-bottom: 4rem;
}
.h1 {
  margin-top: -0.1rem;
  color: rgb(60, 60, 60);
  text-align: left;
  /* padding-left: 1rem; */
  padding-top: 2rem;
  font-size: 1.2rem;
}

.logoCont {
  background-color: white;
  padding: 1rem;
}
.logoSubCont {
  /* grid-column: 2/-1; */
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto;
  gap: 1rem;
}
.frontCont {
  grid-column: 1/-1;
  grid-row: 1/2;
  display: grid;
  gap: 0.5rem;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: max-content;
  /* border: 0.1rem solid rgb(60, 60, 60);
  border-radius: 2rem; */
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  padding-bottom: 1rem;
}
.h2 {
  grid-column: 1/-1;
  grid-row: 1/2;
  font-size: 0.8rem;
  /* text-align: left; */
  color: white;
  background-color: rgb(60, 60, 60);
  padding: 0.2rem;
  margin: 0;
}
.backCont {
  grid-column: 1/-1;
  grid-row: 2/3;
  display: grid;
  gap: 0.5rem;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: max-content;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  padding-bottom: 1rem;
}
.otherCont {
  grid-column: 1/-1;
  grid-row: 3/4;
  display: grid;
  gap: 0.5rem;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: max-content;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  padding-bottom: 1rem;
}
.pic {
  width: 1.8rem;
  padding: 0.3rem;
  opacity: 0.7;
  margin: 0 auto;
}
.iconTitle {
  display: grid;
  grid-template-rows: max-content max-content;
  font-size: 0.7rem;
  margin: 0 auto;
}
@media only screen and (min-width: 941px) {
  .mainCont {
    background-color: none;
    width: 100%;
    text-align: center;
    margin: 0 0 5rem 0;

    /* padding-bottom: 2rem; */
  }
  .h1 {
    margin-top: 0;
    color: rgb(60, 60, 60);
    background-color: white;
    text-align: left;
    padding-left: 0;
    padding-top: 0;
    font-size: 3.5rem;
  }
  .logoCont {
    padding: 2rem 2rem 4rem 2rem;
  }
  .logoSubCont {
    grid-template-columns: repeat(3, 1fr);
  }
  .frontCont {
    grid-column: 1/2;
    grid-row: 1/2;
  }
  .h2 {
    grid-column: 1/-1;
    grid-row: 1/2;
    font-size: 0.8rem;
    color: white;
    background-color: rgb(60, 60, 60);
    padding: 0.2rem;
    margin: 0;
  }
  .backCont {
    grid-column: 2/3;
    grid-row: 1/2;
  }
  .otherCont {
    grid-column: 3/-1;
    grid-row: 1/2;
  }
}
